<template>
  <div id="app">
    <div class="nav">
      {{curTopic.type}}题&nbsp;&nbsp;索引{{activeIndex}}
    </div>
    <div class="container">
      <div class="title">
        {{curTopic.title}}
      </div>
      <div class="option-group">
        <div 
          v-if="curTopic.type === '单选' || curTopic.type === '判断' " 
          class="radio"
        >
          <RadioGroup @change="onRadioChange" :disabled="radioDisabled" v-model="radio">
            <Radio
              v-for="item in curTopic.options"
              :key="item.option" 
              :name="item.option"
            >
              {{item.option}}：{{item.val}}
            </Radio>
          </RadioGroup>
        </div>
        <div v-if="curTopic.type === '多选'" class="checkbox">
          <CheckboxGroup v-model="checkbox">
            <Checkbox 
              shape="square"
              v-for="item in curTopic.options"
              :key="item.option" 
              :name="item.option"
            >
              {{item.option}}：{{item.val}}
            </Checkbox>
          </CheckboxGroup>
          <div class="buttons">
            <div @click="submit" class="button">
              提交
            </div>
          </div>
        </div>
      </div>
      <div v-show="curTopic.tips" class="tips">
        知识要点：{{curTopic.tips}}
      </div>
      <div v-show="finish" class="tips">
        <p 
          :style="{color: correctTips === '回答正确' ? '#2ed573' : '#ff4757'}"
        >
          {{correctTips}}
        </p>
        <p>正确答案： {{curTopic.answer}}</p>
      </div>
      <div class="buttons">
        <div @click="perv" class="button">
          上一题
        </div>
        <input v-model="activeIndex"  />
        <div @click="next" class="button">
          下一题
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import dataSource from '@/assets/json/data'
import internal from 'stream';
import { RadioGroup, Radio, Checkbox, CheckboxGroup } from 'vant';
export default {
  name: 'App',
  components: {
    RadioGroup,
    Radio,
    Checkbox,
    CheckboxGroup 
  },
  data() {
    return {
      dataSource,
      activeIndex: 0,
      radio: '',
      radioDisabled: false,
      checkbox: [],
      checkBoxDisabled: false,
      finish: false,
      correctTips: '123'
    }
  },
  methods: {
    reset() {
      this.radio =  ''
      this.radioDisabled = false
      this.checkbox =  []
      this.checkBoxDisabled = false
    },
    onRadioChange(val) {
      if (!val) return
      this.radioDisabled = true
      this.finish = true
      if (val !== this.curTopic.answer) {
        this.correctTips = '回答错误'
      } else {
        this.correctTips = '回答正确'
      }
    },
    next() {
      if (this.activeIndex >= this.dataSource.length -1) 
      return
      this.activeIndex++
    },
    perv() {
      if (this.activeIndex === 0)
      return 
      this.activeIndex--
    },
    submit() {
      if (this.checkBoxDisabled) return
      const checkbox = new Set(this.checkbox)
      const answer = this.curTopic.answer.split('')
      let result = true
      if (checkbox.size !== answer.length) {
        result = false
      } else {
        answer.forEach(item => {
          if (!checkbox.has(item)) {
            result = false
          }
        })
      }
      this.checkBoxDisabled = true
      this.finish = true
      if (result) {
        this.correctTips = '回答正确'
      } else {
        this.correctTips = '回答错误'
      }
    }
  },
  watch: {
    activeIndex(index) {
      this.finish = false
      this.correctTips = ''
      this.reset()
    }
  },
  computed: {
    curTopic() {
      return this.dataSource[this.activeIndex]
    }
  },
  mounted() {
  }
}
</script>

